import React from 'react'
import { Breadcrumb, BreadCrumbPart, PageHeader, PageHeaderContent } from '@aserto/console-common'

import { useActiveConfigurationName } from '../../services/ActiveConfiguration'
import { Header } from './styles'

const HeaderComponent: React.FC = () => {
  const { configurationName } = useActiveConfigurationName()
  const breadcrumbParts: BreadCrumbPart[] = [{ text: `Configuration/${configurationName}` }]

  return (
    <Header>
      <PageHeader hasBorderBottom={true}>
        <PageHeaderContent>
          <Breadcrumb breadcrumbParts={breadcrumbParts} />
        </PageHeaderContent>
      </PageHeader>
    </Header>
  )
}

export default HeaderComponent
